<template>
  <div class="add">
      <el-form :model="formData" label-width="80px">
          <el-form-item label="客户姓名">
              <el-input v-model="formData.name"></el-input>
          </el-form-item>
          <el-form-item label="联系电话">
              <el-input v-model="formData.phone"></el-input>
          </el-form-item>
          <el-form-item label="身份证号">
              <el-input v-model="formData.idNum" @keyup.enter.native="uploadData" v-if="$route.name == '客户添加页'"></el-input>
              <el-input v-model="formData.idNum" @keyup.enter.native="updateData" v-if="$route.name == '客户修改页'"></el-input>
          </el-form-item>
          <el-form-item>
              <el-button @click="uploadData" v-if="$route.name == '客户添加页'">提交</el-button>
              <el-button @click="updateData" v-if="$route.name == '客户修改页'">保存修改</el-button>
          </el-form-item>
      </el-form>
  </div>
</template>
<script>
export default {
  data() {
      return {
          formData:{
              name:'',
              phone:'',
              idNum:''
          }
      }
  },
  methods: {
      uploadData() {
          this.$axios.post('addClient',this.formData).then(res => {
              if(res.code == 200){
                  this.$message({
                    message: '恭喜你，添加成功!',
                    type: 'success'
                  });
                  setTimeout(() => {
                      this.$router.push('/clients')
                  },1000)
              }
          })
      },
      getData() {
          let {id} = this.$route.query;
          this.$axios.get('getClient',{id}).then(res => {
              this.formData = res.data[0];
          })
      },
      updateData() {
          let {id} = this.$route.query;
          this.$axios.post('updateClient',this.formData).then(res => {
              console.log(res)
              if(res.code == 200){
                  this.$message({
                    message: '恭喜你，修改成功!',
                    type: 'success'
                  });
                  setTimeout(() => {
                      this.$router.push('/clients')
                  },1000)
              }
          })
      }
  },
  created() {
      if(this.$route.name == '客户修改页'){
          this.getData()
      }
  },
  watch:{
      $route(to,from,next) {
          if(to.name == '客户添加页'){
              this.formData = {
                  name:'',
                  phone:'',
                  idNum:''
              }
          }
      }
  }
}
</script>
<style scoped>
.add{
    margin: 20px;
}
</style>



